.footerContainerInstagramFeed {
    display: flex;
    background-color: white;
    flex-direction: row;
    justify-content: space-between;
    padding: 0px 8px;
}

.footerOptionInstagramFeed {
    display: flex;
    flex-direction: row;
    height: 39px;
    width: 40%;
    justify-content: space-around;
}

.likeOptionImgInstagramFeed {
    background-image: url("../../assets/instagramOptions.png");
    background-position: -49px -2460px;
    background-size: 97px 3207px;
    width: 24px;
    height: 24px;
    background-repeat: no-repeat;
    display: inline-block;
}

